// src/App.js
import React, { Component } from "react";
import "./App.css";
import Scene3d from "./Components/Scene3d";
import Controls from "./Components/Controls";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      optionNames: ["home", "engines", "part1", "part2", "part3", "interstage"],
      optionValues: [
        [],
        ["white", "light_grey", "grey", "black"],
        ["grey", "white", "black", "blue", "red"],
        ["grey", "white", "black", "blue", "red"],
        ["grey", "white", "black", "blue", "red"],
        ["grey", "white", "black", "blue", "red"]
      ]
    };
  }

  render() {
    return (
      <div className="App">
        <Scene3d />
        <Controls
          options={this.state.optionNames}
          optionValues={this.state.optionValues}
        />
      </div>
    );
  }
}

export default App;
